<template lang="pug">
 div
    ul.tag-pagination.ml-5.nav.nav-tabs 
      li  カテゴリー選択 
          i.fas.fa-arrow-circle-right
      li.nav-item.ml-5(:class="{'active': $store.state.line.head =='ind'}"  @click="changeHead('ind')")
        a.nav-link 産業別
      li.nav-item(:class="{'active': $store.state.line.head =='cli'}"  @click="changeHead('cli')") 
        a.nav-link クライアント
      li.nav-item(:class="{'active': $store.state.line.head =='pro'}") 
        a.nav-link 商品名  
    a(:href="download_api" download v-show="!isloading")   <!-- loading  --> 
       img(src="/static/images/download.svg" @click="download_loading"  ).download
    a(v-show="isloading")     <!-- loading 中 -->
       .spinner-border.spinner-border-sm.download    
    transition(name="page" mode="out-in")
        keep-alive
            router-view(:key="$route.path")   <!--  line頁面-->
</template>

<script>
export default {
  data(){
      return{
          isloading: false //loading 變數
      }
   },

    computed:{
     download_api(){
        return  this.$store.state.line.download_api ; //預設下載api取得
     }
   },
   methods:{
      changeHead(nowhead){ // 改變指標類別
          this.$store.dispatch('line/getHead',nowhead)
      },
      download_loading(){ // 下載api
         this.isloading = !this.isloading;
         let api = this.download_api ; 
         this.$http.get(api).then((res) => {
                if(res){
                    this.isloading = !this.isloading;
                    alert('下載成功');
                }else{
                    return 
                }

           })
      }

   }
 
}
</script>




