              )
            i.fas.fa-eye(@click="$chart_scale('cardCTR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCTR
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCVR
          .card-body
            h6.card-title CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCVR         
  .row 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPC
          .card-body
            h6.card-title CPC
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPC'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPC')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPC 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPM
          .card-body
            h6.card-title CPM
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPM'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM                
  .row 
    .col-xl-6.grid-margin.stretch-card
      .col-sm-12.ml-3
        .card.cardCPO
          .card-body
            h6.card-title CPO
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPO'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPO')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPO 
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCOST_CPO
          .card-body
            h6.card-title COST x CPO
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCOSTCPO'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCOST_CPO')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCOST_CPO 
  .row
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPO_CV
          .card-body
            h6.card-title CPO x CV
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPOCV'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPO_CV')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPO_CV
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPO_CPM
          .card-body
            h6.card-title CPO x CPM
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPOCPM'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPO_CPM')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPO_CPM
  .row
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCTR_CVR
          .card-body
            h6.card-title CTR x CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCTRCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCTR_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCTR_CVR
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPM_CVR
          .card-body
            h6.card-title CPM x CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPMCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM_CVR
  .row 
    .col-xl-6.grid-margin.stretch-card(v-show="head.length == 1")
      .col-sm-12.ml-3
        .card.cardCPM_CTR_CVR
          .card-body
            h6.card-title CPM x CTR x CVR
              i.fas.fa-question.ml-3(
                type="button",
                data-toggle="modal",
                data-target="#exampleModal",
                @click="Description = 'cardCPMCTRCVR'"
              )
            i.fas.fa-eye(@click="$chart_scale('cardCPM_CTR_CVR')")
            .spinner-border.spinner-border-sm.chart_loading(v-if="isLoading")
            canvas#chartjsLineCPM_CTR_CVR 
            
  #exampleModal.modal.fade(  
    tabindex="-1",
    role="dialog",
    aria-labelledby="exampleModalLabel",
    aria-hidden="true"
  )  <!-- ?號 的提示盒子 -->
    .modal-dialog.modal-dialog-centered(
      role="document",
      v-show="Description == item.name",
      v-for="item in Description_context"
    )
      .modal-content
        .modal-header
          h5#exampleModalLabel.modal-title {{ item.title }}
          button.close(type="button", data-dismiss="modal", aria-label="Close")
            span(aria-hidden="true") &times;
        .modal-body  <!-- 盒子內容 -->
           span(v-html="item.context1") 
           span(v-html="item.context2") 
           span(v-html="item.context3")
           span(v-html="item.context4")
           span(v-html="item.context5")
           span(v-html="item.context6")
           span(v-html="item.context7")
           span(v-html="item.context8")
           span(v-html="item.context9")
        .modal-footer
          button.btn.btn-secondary(type="button", data-dismiss="modal") Close
  keep-alive 
    LINETABLE(:line="line_df", v-if="sendNow", :head="head")  <!-- 表格 -->
</template>

<script>
import $ from "jquery";
import LINETABLE from "@/components/table/line-table";
export default {
  components: {
    LINETABLE,
  },
  data() {
    return {
      line_df: [], // 表格
      head: [], // 表格
      sendNow: false, // 表格狀態
      Description: "",
      Description_context: [ // 提示框的內容
        { title: "CPO", name: "cardCPO" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Formula：【Cost】 / 【CV】"},
        { title: "CV", name: "cardCV" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Data Column：【網站購買】"},
        { title: "COST", name: "cardCOST" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Data Column：【花費金額 (TWD)】"},
        { title: "CVR", name: "cardCVR" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:`Formula：【CV】 / 【Click】<br>` , context3:"Format：%" },
        { title: "CTR", name: "cardCTR" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:`Formula：【Click】 / 【IMP】<br>` , context3:"Format：%"},
        { title: "CPM", name: "cardCPM" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Formula：【Cost】 / 【IMP】 * 1000"  },
        { title: "CPC", name: "cardCPC" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Formula：【Cost】 / 【Click】" },
        { title: "IMP", name: "cardIMP" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Data Column：【曝光次數】" },
        { title: "CLICK", name: "cardCLICK" ,context1:`Data Source：媒体管理画面、素材報告 <br>` , context2:"Data Column：【連結點擊次數】"},
        { title: "COST x CPO", name: "cardCOSTCPO" ,context1:`&nbsp;&nbsp; ● COST <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Column：【花費金額 (TWD)】 <br>` , context4:`&nbsp;&nbsp; ● CPO <br>` , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【CV】`},
        { title: "CPO x CV", name: "cardCPOCV" ,context1:`&nbsp;&nbsp; ● CPO <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【CV】 <br>` , context4:`&nbsp;&nbsp; ● CV <br>` , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Data Column：【網站購買】` },
        { title: "CPO x CPM", name: "cardCPOCPM" ,context1:`&nbsp;&nbsp; ● CPO <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【CV】 <br>` , context4:`&nbsp;&nbsp; ● CPM <br>` , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【IMP】 * 1000`},
        { title: "CTR x CVR", name: "cardCTRCVR" ,context1:`&nbsp;&nbsp; ● CTR <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Click】 / 【IMP】 <br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% <br> ` , context4:`&nbsp;&nbsp; ● CPM <br>` , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Cost】 / 【IMP】 * 1000` },
        { title: "CPM x CVR", name: "cardCPMCVR" ,context1:`&nbsp;&nbsp; ● CPM <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Cost】 / 【IMP】 * 1000 <br>` , context4:`&nbsp;&nbsp; ● CVR <br>` , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【CV】 / 【Click】<br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% ` },
        { title: "CPM x CTR x CVR", name: "cardCPMCTRCVR" ,context1:`&nbsp;&nbsp; ● CPM <br>` , context2:` &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>` , context3:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Formula：【Click】 / 【IMP】 <br>` , context4:`&nbsp;&nbsp; ● CTR <br>`  , context5:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>`,context6:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【Click】 / 【IMP】 <br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% <br> ` ,context7:`&nbsp;&nbsp; ● CVR <br>`,context8:"&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Data Source：媒体管理画面、素材報告 <br>",context9:`&nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;  Formula：【CV】 / 【Click】<br> &nbsp;&nbsp;&nbsp;&nbsp; &nbsp;&nbsp; Format：% ` },
      ],
      //圖表盒子變數
      a_CPO: new_cpo,
      a_CV: new_cv,
      a_COST: new_cost,
      a_CVR: new_cvr,
      a_CTR: new_ctr,
      a_CPM: new_cpm,
      a_CPC: new_cpc,
      a_IMP: new_imp,
      a_CLICK: new_click,
      a_COST_CPO: new_COST_CPO,
      a_CPO_CV: new_CPO_CV,
      a_CPO_CPM: new_CPO_CPM,
      a_CTR_CVR: new_CTR_CVR,
      a_CPM_CVR: new_CPM_CVR,
      a_CPM_CTR_CVR: new_CPM_CTR_CVR,
    };
  },
  watch: {
    dateType() {
      const vm = this;
      vm.upDate();
    },
    new_data() {
      this.upDate();
    },
    nowHead() {
      this.upDate();
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.line.isLoading; // loading 變數獲取
    },
    new_data() {
      return this.$store.state.line.data; //  獲取當前 fb data
    },
    dateType() {
      return this.$store.state.line.dateType; // 獲取當前date 類別
    },
    nowHead() {
      return this.$store.state.line.head; // 獲取當前指標值
    },
  },
  methods: {
    upDate() { // 圖表的update
      console.time("fb updata的執行時間");
      const vm = this;
      let line_df = this.$store.state.line.data; // cros的資料來源
      // 判斷當前時間類別
      if (this.$store.state.line.head == "cli") {
        vm.head = line_df[0].clients;
      }
      if (this.$store.state.line.head == "ind") {
        vm.head = line_df[0].industrytype;
      }
      vm.line_df = line_df[1];
      line_df = line_df[1];
      /*
      if (!vm.dateType) {
        fb_df = fb_df[1];
      }
      if (vm.dateType == "m") {
        fb_df = fb_df[2];
      }
      if (vm.dateType == "q") {
        fb_df = fb_df[3];
      }
      */
      vm.sendNow = true;
      let timeBox = [];
      let fb_charts = [[], [], [], [], [], [], [], [], []];
      // 圖表顏色
      let color_all = [
        "#f38181",
        "#fce38a",
        "#eaffd0",
        "#a8e6cf",
        "#fdffab",
        "#ffd3b6",
        "#ffaaa5",
        "#d4a5a5",
        "#ffecda",
        "#f9ffea",
        "#a6d0e4",
        "#ff6f3c",
        "#ff9a3c",
        "#ffc93c",
        "#17b978",
        "#a7ff83",
      ];
      // 依據當前客戶數產生陣列與預設資料
      for (let head in vm.head) {
        for (let charts in fb_charts) {
          fb_charts[charts].push({
            data: [],
            label: vm.head[head],
            borderColor: color_all[head] || "#7ee5e5",
            backgroundColor: "#fff",
            fill: false,
          });
        }
        let [
          cpoBox,
          cvBox,
          costBox,
          cvrBox,
          ctrBox,
          cpmBox,
          cpcBox,
          impBox,
          clickBox,
        ] = fb_charts;
        // 如果指標是客戶別
        if (this.$store.state.line.head == "cli") {
          clientname();
        }
        // 如果指標是產業別
        if (this.$store.state.line.head == "ind") {
          industrytype();
          mergeBox();
        }
        //
        //客戶別函數
        function clientname() {
          line_df.forEach(function (item) {
            if (item.clientname.includes(vm.head[head]) == 1)
              timeBox.push(item.date);

            if (item.clientname == cpoBox[head].label)
              cpoBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpo * 100) / 100,
              });
            if (item.clientname == cvBox[head].label)
              cvBox[head].data.push({
                x: item.date,
                y: item.cv,
              });
            if (item.clientname == costBox[head].label)
              costBox[head].data.push({
                x: item.date,
                y: Math.round(item.cost * 100) / 100,
              });
            if (item.clientname == cvrBox[head].label)
              cvrBox[head].data.push({
                x: item.date,
                y: Math.round(item.cvr * 100) / 100,
              });
            if (item.clientname == ctrBox[head].label)
              ctrBox[head].data.push({
                x: item.date,
                y: Math.round(item.ctr * 100) / 100,
              });
            if (item.clientname == cpmBox[head].label)
              cpmBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpm * 100) / 100,
              });
            if (item.clientname == cpcBox[head].label)
              cpcBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpc * 100) / 100,
              });
            if (item.clientname == impBox[head].label)
              impBox[head].data.push({
                x: item.date,
                y: item.impressions,
              });
            if (item.clientname == clickBox[head].label)
              clickBox[head].data.push({
                x: item.date,
                y: item.clicks,
              });
          });
        }
        //產業別涵式
        function industrytype() {
          line_df.forEach(function (item) {
            if (item.industrytype.includes(vm.head[head]) == 1)
              timeBox.push(item.date);
            if (item.industrytype == cpoBox[head].label)
              cpoBox[head].data.push({
                x: item.date,
                y: 0,
              });
            if (item.industrytype == cvBox[head].label)
              cvBox[head].data.push({
                x: item.date,
                y: item.cv,
              });
            if (item.industrytype == costBox[head].label)
              costBox[head].data.push({
                x: item.date,
                y: Math.round(item.cost * 100) / 100,
              });
            if (item.industrytype == cvrBox[head].label)
              cvrBox[head].data.push({
                x: item.date,
                y: Math.round(item.cvr * 100) / 100,
              });
            if (item.industrytype == ctrBox[head].label)
              ctrBox[head].data.push({
                x: item.date,
                y: Math.round(item.ctr * 100) / 100,
              });
            if (item.industrytype == cpmBox[head].label)
              cpmBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpm * 100) / 100,
              });
            if (item.industrytype == cpcBox[head].label)
              cpcBox[head].data.push({
                x: item.date,
                y: Math.round(item.cpc * 100) / 100,
              });
            if (item.industrytype == impBox[head].label)
              impBox[head].data.push({
                x: item.date,
                y: item.impressions,
              });
            if (item.industrytype == clickBox[head].label)
              clickBox[head].data.push({
                x: item.date,
                y: item.clicks,
              });
          });
        }
        // 合併重複的日期  
        function mergeBox() {
          let cpoData = [];
          let cvData = [];
          let costData = [];
          let impData = [];
          let clicksData = [];
          let ctrData = [];
          let cvrData = [];
          let cpcData = [];
          let cpmData = [];
          cpoData.push(cpoBox[head].data);
          cvData.push(cvBox[head].data);
          costData.push(costBox[head].data);
          impData.push(impBox[head].data);
          clicksData.push(clickBox[head].data);
          ctrData.push(ctrBox[head].data);
          cvrData.push(cvrBox[head].data);
