import Vue from 'vue'
import Router from 'vue-router'

import LOGIN from '@/components/page/login'


//menu 
import SIDEBAR from '@/components/menu/sidebar'
import TOPBAR from '@/components/menu/topbar'
//DORENTO 
import CROS from '@/components/page/cros/cros'
import CROSDATA from '@/components/page/cros/cros-data'

// FB 
import FB  from '@/components/page/fb/fb'
import FBDATA  from '@/components/page/fb/fb-data'

// LINE
import LINE from '@/components/page/line/line'
import LINEDATA  from '@/components/page/line/line-data'

// filter
import CROSFILTER from '@/components/filter/cros-filter'
import FACEBOOKFILTER from '@/components/filter/fb-filter'
import LINEFILTER from '@/components/filter/line-filter'

Vue.use(Router)



export default new Router({
 // mode:history,
  routes: [
    /*
    {
      path: "*",
      redirect: "login"
    },
    */
    {
      path: '/login',
      name: 'login',
      components: {
        default: LOGIN ,
      }
    },
    { //cros 頁面
      path: '/', 
      components: {
        default: CROS ,
        sidebar: SIDEBAR, 
        filter: CROSFILTER,
        meta: { requiresAuth: true }
        
      },
      children:[
        {
          name:"cros",
          path:"/",
          component: CROSDATA,
          meta: { requiresAuth: true }
        },
      ]
    },
    { // fb 頁面
      path: '/fb-page',
      components:{
        default: FB ,
        sidebar: SIDEBAR,
        filter: FACEBOOKFILTER,
      },
      children:[
        {
          path:"/", 
          name: 'fb-page',
          component: FBDATA
        }
       
       ]
    },
    {  // line 頁面
      path: '/line-page',
      components:{
        default: LINE ,
        sidebar: SIDEBAR,
        filter: LINEFILTER,
      },
      children:[
        {
          path:"/", 
          name: 'line-page',
          component: LINEDATA
        }
       
       ]

    }
  ]
})



